import './App.css';
import React, { useState } from 'react';
import { Nav } from 'react-bootstrap';
import TabContainer from './questionAnswer/section/TabContainer';
import {sectionsLaparoskopie,sectionsCT} from './questionAnswer/sectionData';
import Example from './chat/Example'

const App = () => {
    const [activeKey, setActiveKey] = useState('tab1');

    const handleSelect = (selectedKey) => {
        setActiveKey(selectedKey);
    };

    return (
        <>
            <Nav variant="tabs" activeKey={activeKey} onSelect={handleSelect}>
                <Nav.Item>
                    <Nav.Link eventKey="tab1">Tab 1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="tab2">Tab 2</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="tab3">Tab 3</Nav.Link>
                </Nav.Item>
            </Nav>

            {activeKey === 'tab1' && <TabContainer sections={sectionsCT} />}
            {activeKey === 'tab2' && <TabContainer sections={sectionsLaparoskopie} />}
            {activeKey === 'tab3' && <Example/>}
        </>
    );
};

export default App;
