import { useState } from 'react';
import { useEffect } from 'react';
import { useRef } from 'react';
import Button from 'react-bootstrap/Button';
import Collapse from 'react-bootstrap/Collapse';

function CollapseButton ({ vocab,index }) {
    const [open, setOpen] = useState(false);
    const buttonRef = useRef(null);

    useEffect(() => {
        if (buttonRef.current) {
            if (open) {
                buttonRef.current.style.margin = '0px';
                buttonRef.current.style.marginRight = '5px';
                buttonRef.current.style.padding = '0px';
            } else {
                buttonRef.current.style.marginRight = '5px';
                buttonRef.current.style.padding = '10px';
                buttonRef.current.style.paddingRight = '20px';
            }
        }
    }, [open]);

    return (
        <>
            <Button
                ref={buttonRef}
                onClick={() => setOpen(!open)}
                aria-controls={`collapse-text-${index}`}
                aria-expanded={open}
            >
                <Collapse in={open}>
                    <div id={`collapse-text-${index}`}>
                        {vocab.thai}
                        ({vocab.german})
                    </div>
                </Collapse>
            </Button>
            
        </>
      );
}
    
export default CollapseButton;