import React from 'react';
import {compare} from './compare'

const ComparisonResult = ({ userInput, germanText }) => {
    function renderComparison() {
        const matches= compare(germanText,userInput)
        const words = germanText.split(/\s+/);
        const color1="green"
        const color2="gray"
        return words.map((word, index) => {
                const style = { color: matches[index] === 1 ? color1 : color2 };
                return <span key={index} style={style}>{word}</span>;
            });
    };

    return (
        <div>
        {renderComparison().map((element, index) => (
            <React.Fragment key={index}>
                {element}{' '}
            </React.Fragment>
        ))}
        </div>
    );
};

export default ComparisonResult;
