import React from 'react';
import { useState } from 'react';
import CollapseButton from './CollapseButton';
import './CollapseButton.css'
import { Card } from 'react-bootstrap';
import './Section.css'
import ComparisonResult from './ComparisonResult'

function Section ({ id,germanText, thaiText, vocabulary }) {
    const [userInput, setUserInput] = React.useState('');
    const [showResult, setShowResult] = useState(false);

    const handleInputChange = (event) => {
        setUserInput(event.target.value);
        // Reset showResult if the input changes
        setShowResult(false);
    };

    const compareTexts = () => {
        // Set showResult to true to display the comparison result
        setShowResult(true);
    };

    return (
        <Card>
            <p className="germanText">{germanText}</p>
            <p className="foreignText">{thaiText}</p>
            <hr />
            <div className="cardVocabulary">
            {vocabulary.map((vocab, index) => (
                <CollapseButton key={index}
                                vocab={vocab}
                                index={index} 
                />
            ))}
            </div>
            <input
                type="text"
                value={userInput}
                onChange={handleInputChange}
                className="form-control"
                placeholder="Trage hier deine Antwort ein"
            />
            <button onClick={compareTexts}>
                Vergleichen
            </button>
            {showResult && <ComparisonResult userInput={userInput} germanText={germanText} />}
        </Card>
    );
};

export default Section;
