import React, { useState, useEffect, useRef } from 'react';
import { MessageBox, Input, Button } from 'react-chat-elements';
import 'react-chat-elements/dist/main.css';

// Simulated data
const daten = {
    "Gespräch": {
        "FragenListe": [
          "Welche Symptome haben Sie?",
          "Wie lange haben Sie diese Symptome schon?",
          "Haben Sie weitere Beschwerden?",
          "Gibt es Vorerkrankungen, die wir kennen sollten?"
        ],
        "AntwortenOptionen": {
          "Welche Symptome haben Sie?": ["Kopfschmerzen", "Schwindel", "Übelkeit"],
          "Wie lange haben Sie diese Symptome schon?": ["Ein paar Tage", "Einige Wochen", "Seit heute Morgen"],
          "Haben Sie weitere Beschwerden?": ["Ja, ich fühle mich müde", "Nein, das ist alles", "Manchmal Übelkeit"],
          "Gibt es Vorerkrankungen, die wir kennen sollten?": ["Ja, Bluthochdruck", "Nein, keine", "Ja, Diabetes Typ 2"]
        }
      }
};

function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function Example() {
    const [messages, setMessages] = useState([]);
    const [inputMessage, setInputMessage] = useState('');
    const [gespraechsverlauf, setGespraechsverlauf] = useState({});
    const messagesEndRef = useRef(null);  // Reference to the end of the messages

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }

    useEffect(() => {
        scrollToBottom();
    }, [messages]); // Triggered every time the 'messages' state changes


    // Fetch patient's response (simulate server call)
    const fetchPatientResponse = async (frage) => {
        // Simulate a server response delay
        const response = await new Promise(resolve => {
            setTimeout(() => resolve(frageStellen(frage)), 500);
        });

        const responseMessage = {
            position: 'left',
            type: 'text',
            text: response,
            date: new Date(),
        };

        setMessages(prevMessages => [...prevMessages, responseMessage]);
    };

    // Handle sending message (doctor's question)
    const onSend = () => {
        if (inputMessage.trim()) {
            const questionMessage = {
                position: 'right',
                type: 'text',
                text: inputMessage,
                date: new Date(),
            };

            setMessages(prevMessages => [...prevMessages, questionMessage]);
            fetchPatientResponse(inputMessage);
            setInputMessage('');
        }
    };

    // Function to handle question and get a random answer
    const frageStellen = (frage) => {
        let antworten = gespraechsverlauf[frage];

        if (antworten && antworten.length === 0) {
            return "Diese Frage wurde bereits beantwortet.";
        }

        if (!antworten) {
            antworten = daten["Gespräch"]["AntwortenOptionen"][frage]?.slice() || [];
        }

        const gewaehlteAntwort = antworten[getRandomInt(0, antworten.length - 1)];
        antworten = antworten.filter(a => a !== gewaehlteAntwort);

        setGespraechsverlauf(prev => ({ ...prev, [frage]: antworten }));
        return gewaehlteAntwort;
    };

    return (
        <div>
            <div style={{ height: '400px', overflow: 'auto' }}>
                {messages.map((msg, index) => (
                    <MessageBox
                        key={index}
                        position={msg.position}
                        type={msg.type}
                        text={msg.text}
                        date={msg.date}
                    />
                ))}
                <div ref={messagesEndRef} />  {/* Invisible element at the end of messages */}
            </div>
            <Input
                placeholder="Stellen Sie eine Frage..."
                value={inputMessage}
                onChange={e => setInputMessage(e.target.value)}
                rightButtons={
                    <Button
                        color='white'
                        backgroundColor='black'
                        text='Senden'
                        onClick={onSend}
                    />
                }
            />
        </div>
    );
}

export default Example;
