export function compare(str1, str2) {
    // Split the strings into arrays of words
    const words1 = str1.split(/\s+/);
    const words2 = str2.split(/\s+/);
  
    // Initialize the results array with 0s
    const matches = new Array(words1.length).fill(0);
  
    // Iterate through the words in the first array
    for (let i = 0; i < words1.length; i++) {
      // Find the index of the first match in the second array
      const indexInWords2 = words2.indexOf(words1[i]);
  
      if (indexInWords2 !== -1) {
        // Mark the current word as a match
        matches[i] = 1;
  
        // Check subsequent words for consecutive matches
        let f = i + 1; // Next position in words1
        let s = indexInWords2 + 1; // Next position in words2
  
        while (
          f < words1.length &&
          s < words2.length &&
          words1[f] === words2[s]
        ) {
          matches[f] = 1;
          f++;
          s++;
        }
  
        // Skip already checked words in words1
        i = f - 1;
      }
    }
  
    return matches;
}